.card {
  width: 300px;
  position: relative; }
  .card img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    object-position: center bottom;
    background-color: #444; }
  .card:after {
    content: "";
    display: block;
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 300px;
    background-color: rgba(1, 37, 29, 0.75);
    opacity: 0;
    transition: opacity 0.5s ease-in-out, box-shadow 0.5s ease-in-out; }
  .card a {
    position: absolute;
    top: 140px;
    left: 50%;
    z-index: 4;
    opacity: 0;
    transform: translateX(-50%) rotateZ(180deg);
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, left 0.5s ease-in-out; }
  .card:hover:after, .card:active:after {
    opacity: 1;
    box-shadow: 0 0 10px 1px #2eaa99; }
  .card:hover a, .card:active a {
    opacity: 1;
    transform: translateX(-50%) rotateZ(0deg); }
  .card:hover .tags, .card:active .tags {
    top: 100%;
    opacity: 1; }
  .card:hover .codeUrl, .card:active .codeUrl {
    left: 10%; }
  .card .tags {
    background: #111;
    position: absolute;
    width: 100%;
    z-index: 5;
    left: 0;
    top: 90%;
    opacity: 0;
    transition-duration: 0.5s;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px; }
  .card .codeUrl {
    top: 10px;
    transform: translateX(-50%);
    left: -20%; }
